'use strict';

define(['pim/form/common/creation/modal', 'oro/messenger', 'oro/mediator', 'oro/translator'], function (BaseModal, messenger, mediator, __) {
  return BaseModal.extend({
    /**
     * {@inheritdoc}
     */
    confirmModal: function confirmModal(modal, deferred) {
      var _this = this;

      this.save().done(function () {
        modal.close();
        modal.remove();
        deferred.resolve();
        messenger.notify('success', __(_this.config.successMessage));
        mediator.trigger('datagrid:doRefresh:' + _this.config.gridName);
      });
    }
  });
});