'use strict';

define(['underscore', 'pim/controller/front', 'pim/form-builder'], function (_, BaseController, FormBuilder) {
  return BaseController.extend({
    initialize: function initialize(options) {
      this.options = options;
    },

    /**
     * {@inheritdoc}
     */
    renderForm: function renderForm() {
      var _this = this;

      return FormBuilder.build('pim-' + this.options.config.entity + '-index').then(function (form) {
        form.setElement(_this.$el).render();
        return form;
      });
    }
  });
});